import { axiosClient } from "@rp/react/axios-client"
import { useQuery } from "@tanstack/react-query"
import { PanelConfig } from "./discovery-panel-config.types"

// -------------------
// Constants
// -------------------
const API_KEY = ["panel-config"]
const BASE_URL = "/config/panel-config.json"

// -------------------
// Requests
// -------------------
const get = async () => {
  const { data } = await axiosClient.get<PanelConfig>(BASE_URL)
  return data
}

// -------------------
// Hooks
// -------------------

const usePanelConfig = () => {
  return useQuery({
    queryKey: API_KEY,
    queryFn: get,
    refetchOnWindowFocus: false,
    gcTime: Infinity,
    staleTime: Infinity,
  })
}

export { usePanelConfig }
